<style scoped>
  html,
  body,
  #app {
    width: 100%;
    height: 100%;
  }
  
  .user_login {
    background-image: url(http://192.168.1.118/static/img/backgroundImage.jpg);
    background-size: 120%;
    background-position: center;
    width: 100%;
    height: 100%;
  }
  
  .user_login>.top {
    text-align: center;
    width: 100%;
    height: 50px;
    font-size: 18px;
    color: white;
    font-family: "";
    line-height: 50px;
    width: 100%;
    background: linear-gradient( to right, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0) 10%, rgba(255, 255, 255, 0.3) 20%, rgba(255, 255, 255, 0.4) 30%, rgba(255, 255, 255, 0.5) 40%, rgba(255, 255, 255, 0.5) 50%, rgba(255, 255, 255, 0.5) 60%, rgba(255, 255, 255, 0.4) 70%, rgba(255, 255, 255, 0.3) 80%, rgba(0, 0, 0, 0) 90%, rgba(0, 0, 0, 0) 100%)
  }
  
  .mid {
    width: 100%;
    overflow: hidden;
    margin-top: 100px;
  }
  
  .mid>.midLeft {
    text-align: center;
    width: 20%;
    min-width: 200px;
    float: left;
    background-color: rgba(167, 167, 167, .5);
    margin-left: 12%;
    margin-top: 60px;
    color: white !important;
    font-family: "";
    padding: 20px;
    border-radius: 10px;
  }
  
  .mid>.midRight {
    width: 30%;
    float: right;
    min-width: 252px;
  }
  
  .midLeft>h2 {
    margin-bottom: 15px;
  }
  
  .midRight {
    background-color: rgba(255, 255, 255, .5);
    border-radius: 10px;
    padding: 10px;
    margin-right: 9%;
  }
  
  .userform {
    width: 100%;
  }
  
  .userform>.form_ipt {
    width: 100%;
    margin: 20px 0;
  }
  
  .userform>.form_ipt>span {
    width: 20%;
    display: inline-block;
    font-size: 16px;
  }
  
  .userform>.form_ipt>.form_ipt_txt {
    width: 70%;
    display: inline-block;
    outline: none;
    border: none;
    padding: 14px;
    border-radius: 8px;
  }
  
  .error {
    color: red;
  }
</style>

<template>
  <div class="user_login">
    <div class="top">
      {{$t('tianfu.eng')}}
    </div>
    <div class="mid">
      <div class="midLeft">
        <h2>{{$t('UserLog.login')}}</h2>
        <el-form :model="loginForm" :rules="loginRules" ref="loginForm">
          <el-form-item prop="username">
            <el-input v-model="loginForm.username" name="username" :placeholder="$t('UserLogin.inputID')" auto-complete="on"></el-input>
            <!--///-->
          </el-form-item>

          <el-form-item prop="password">
            <el-input v-model="loginForm.password" type="password" name="password" :placeholder="$t('UserLogin.inputPass')" auto-complete="on"></el-input>
            <!--//-->
          </el-form-item>

          <el-form-item>
            <el-button type="primary" @click="handleLogin">{{$t('UserLogin.log')}}</el-button>
            <!---->
          </el-form-item>
        </el-form>
      </div>
      <div class="midRight">
        <h2>{{$t('UserLog.gs')}}:</h2>
        <p>ASRTTS</p>
        <p>2050</p>
      </div>
    </div>
  </div>
</template>
<script>
  //
  import { loginReq } from '@/api/login'
  export default {
    name: 'login',
    data() {
      return {
        loginForm: {
          username: '',
          password: '',
        },
        loginRules: {
          username: [{
            required: true,
            message: "",
            trigger: "blur"
          }],
          password: [{
            required: true,
            message: '',
            trigger: 'blur'
          }]
        }
      }
    },
    methods: {
      handleLogin() {
        var that = this;
        that.$refs.loginForm.validate((valid) => {
          if(valid) {
            loginReq(this.loginForm.username, this.loginForm.password).then((res) => {
              console.log(res);
              if(res.data.ret == 200) {
                this.$router.push({
                  name: "DefaultHome",
                  params: {
                    username: this.loginForm.username
                  }
                })
              } else {
                this.$message.error("");
              }
            });
          } else {
            this.$message.error("");
            console.log('')
          }
        })
      },
    }
  }
  /*
   * 
   * 
   * 
   * 
   * 
   * 
   * 
   * 
   *
   * */
</script>


