<style scoped>
  .drillTop {
    margin: 10px;
  }
  
  .Nlanguage {
    position: fixed;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    z-index: 100;
    background-color: rgba(0, 0, 0, .5);
  }
  
  .Nlanguage>.NLG {
    position: fixed;
    width: 66%;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background-color: white;
    z-index: 120;
    overflow: hidden;
    border-radius: 9px;
    padding: 15px;
  }
  
  .Nlanguage>.NLG>.left {
    float: left;
    width: 45%;
    text-align: center;
  }
  
  .Nlanguage>.NLG>.right {
    float: right;
    width: 45%;
    height: 400px;
    overflow: auto;
    border-left: 1px solid #cacaca;
    padding-left: 5%;
  }
  
  .target {
    position: fixed;
    text-align: center;
    background-color: rgba(0, 0, 0, .5);
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 100;
  }
  
  .target_add {
    width: 40%;
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background-color: white;
    padding: 25px 50px;
    padding-left: 0;
    border-radius: 9px;
  }
  
  .target_add>h1 {
    font-family: "";
    margin-bottom: 10px;
  }
  
  .show_taskadd {
    width: 40%;
    /*margin: 245px auto 0;*/
    position: fixed;
    left: 50%;
    top: 50%;
    margin-top: -100px;
    margin-left: -23.5%;
    max-height: 300px;
    padding: 25px 50px;
    background-color: white;
    overflow: hidden;
    border-radius: 9px;
  }
  
  .shangR>h1 {
    margin: 10px 0;
  }
  
  .shangR>ul>.asdd {
    /*border: 1px solid red;*/
    padding-left: 30px;
  }
  
  .shangR>ul>.asdd>li>input {
    width: 20px;
    float: left;
    margin: 14px 0 0 -20px;
  }
  
  .shangR>ul>.asdd>li>label {
    width: 100%;
    display: inline-block;
    padding: 10px;
    padding-left: 20px;
  }
  
  .shangR>ul>.asdd:hover {
    background-color: #e3e3e3;
  }
  
  .el-select {
    float: left;
  }
  .taskmid>table>tr>td{
    padding: 0px 10px;
  }
  .wei {
    width: 99.8%;
    text-align: center;
    font-size: 16px;
    color: #6F7180;
    border: 1px solid #e3e3e3;
    border-top: 0;
    padding: 15px 0;
  }
</style>

<template>
  <div class="drill">
    <Breadcrumb>
      <span slot="one">{{$t('index.modelManager')}}</span>
      <!---->
      <span slot="two">{{$t('index.genderModleManager')}}</span>
      <!--//-->
    </Breadcrumb>
    <div class="drillTop">
      <el-form :inline="true" :model="form" class="demo-form-inline" size="mini">
        <el-form-item :label="$t('index.gender')" size="mini">
          <!--//-->
          <el-select v-model="form.mlanguage" :placeholder="$t('index.gender')" size="mini">
            <!--//-->
            <el-option :label="$t('index.gender1')" value="0"></el-option>
            <!--//-->
            <el-option :label="$t('index.gender2')" value="1"></el-option>
            <!--//-->
            <el-option :label="$t('index.all')" value=""></el-option>
            <!--//-->
          </el-select>
        </el-form-item>
        <el-form-item :label="$t('index.trainingState')" size="mini">
          <!--//-->
          <el-select v-model="form.status" :placeholder="$t('index.trainingState')">
            <!--//-->
            <el-option :label="$t('index.unusable')" value="1"></el-option>
            <!--// index.trainingState1-->
            <el-option :label="$t('index.usable')" value="2"></el-option>
            <!--// index.trainingState2-->
            <el-option :label="$t('index.all')" value=""></el-option>
            <!--//-->
          </el-select>
        </el-form-item>
        <el-form-item size="mini">
          <el-button type="primary" @click="modelS" icon="el-icon-search">{{$t('index.search')}}</el-button>
          <!---->
        </el-form-item>
        <el-form-item size="mini">
          <el-button type="primary" @click="initialize">{{$t('drill.init')}}</el-button>
          <!---->
        </el-form-item>
        <!--<el-form-item size="mini">
          <el-button type="primary" @click="newlan">{{$t('index.createGender')}}</el-button>
        </el-form-item>-->
      </el-form>
      <!--
      <div class="target" v-show="target">
        <div class="target_add">
          <h1>{{$t('index.createGender')}}</h1>
          <el-form :model="languagesList" ref="languagesList" label-width="100px" class="demo-ruleForm">
            <el-form-item :label="$t('index.gender')" required>//
              <el-select v-model="languagesList.language" name="language" :placeholder="$t('index.gender')">//
                <el-option :label="$t('index.gender1')" value="0"></el-option>//
                <el-option :label="$t('index.gender2')" value="1"></el-option>//
              </el-select>
            </el-form-item>
            <el-form-item :label="$t('index.description')">//
              <el-input type="textarea" v-model="languagesList.desc"></el-input>
            </el-form-item>
            <el-form-item>
              <el-button type="primary" @click="newla">{{$t('index.creat')}}</el-button>
              <el-button @click="onEsc" type="danger" plain style="margin-left: 10px !important;">{{$t('index.close')}}</el-button>
            </el-form-item>
          </el-form>
        </div>
      </div>
      -->
      <!---->
      <div class="Nlanguage" v-show="nlguage">
        <div class="NLG">
          <h1 style="text-align: center;margin-bottom:10px;">{{$t('index.genderInfo')}}</h1>
          <!---->
          <div class="left">
            <el-form ref="amendLanguagesList" :model="amendLanguagesList" label-width="80px">
              <el-form-item :label="$t('index.gender')">
                <!-- size="mini"-->
                <!--//-->
                <el-select v-model="amendLanguagesList.gender" prop="gender" name="gender" :placeholder="$t('index.gender')">
                  <!--//-->
                  <el-option :label="$t('index.gender1')" value="0"></el-option>
                  <!--//-->
                  <el-option :label="$t('index.gender2')" value="1"></el-option>
                  <!--//-->
                </el-select>
              </el-form-item>
              <el-form-item :label="$t('index.description')">
                <!--//-->
                <el-input type="textarea" prop="desc" v-model="amendLanguagesList.desc"></el-input>
              </el-form-item>
              <el-form-item>
                <el-button plain type="primary" @click="addlangu">{{$t('index.modification')}}</el-button>
                <!---->
                <el-button plain type="danger" @click="onEsc" style="margin-left: 10px !important;">{{$t('index.close')}}</el-button>
                <!---->
              </el-form-item>
            </el-form>
          </div>
          <div class="right">
            <div class="target_right">
              <h3>{{$t('index.modleFileTable')}}</h3>
              <!---->
              <el-upload class="upload-demo" ref="upload" :action="https" :on-success="handleS" :before-upload="beforeAvatarUpload" :file-list="fileList" :data="UpData" accept=".wav,.mp3,.flac" multiple :auto-upload="false">
                <el-button slot="trigger" size="small" type="primary">{{$t('index.chooseFile')}}</el-button>
                <!---->
                <el-button style="margin-left: 10px;" size="small" type="success" @click="submitUpload">{{$t('index.upload')}}</el-button>
                <!---->
                <div slot="tip" class="el-upload__tip">{{$t('index.uploadReq')}}</div>
                <!--.WAV,.MP3,.FLAC-->
              </el-upload>
              <el-button style="margin: 3px 0;" type="primary" @click="Train" :loading="xunlian" :disabled="xli">{{$t('index.training')}}</el-button>
              <!---->
              <template>
                <div class="taskmid">
                  <table>
                    <tr>
                      <th> {{$t('index.fileName')}} </th>
                      <th> {{$t('index.fileSize')}} </th>
                      <th> {{$t('index.operation')}} </th>
                    </tr>
                    <tr v-for="(i,index) in speechi" :key="index">
                      <td> {{i.filename}} </td>
                      <td> {{i.filesize}} </td>
                      <td>
                        <el-button type="text" :title="$t('index.details')"><!-- @click="newTask(i.id)"-->
                          <i class="el-icon-caret-right" style="font-size: 20px;"></i>
                          <!---->
                        </el-button>
                        <el-button type="text" @click="speechDele(i.tsid)" style="color: red;" :title="$t('index.delete')">
                          <i class="el-icon-delete"></i>
                          <!---->
                        </el-button>
                      </td>
                    </tr>
                  </table>
                </div>
                <!--<el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          @prev-click="handlePrevChange"
          @next-click="handleNextChange"
          :current-page="currentPage"
          :page-sizes="[10, 20, 30, 40]"
          :page-size="pagesize"
          layout="total, sizes, prev, pager, next, jumper"
          :total="totalCount">
        </el-pagination>-->
              </template>
              <div class="block">
              </div>
            </div>
          </div>
        </div>
      </div>
      <hr />
      <div class="taskmid">
        <table>
          <tr>
            <th>{{$t('index.gender')}}</th>
            <!---->
            <th>{{$t('index.description')}}</th>
            <!---->
            <th>{{$t('index.trainingState')}}</th>
            <!---->
            <th>{{$t('index.operation')}}</th>
            <!---->
          </tr>
          <tr v-for="(i,index) in tableData" :key="index">
            <td> {{i.gender}}
            </td>
            <td> {{i.desc}}
            </td>
            <td>
              <span v-if="i.status==1">{{$t('index.usable')}}</span>
              <!---->
              <span v-if="i.status==0">{{$t('index.unusable')}}</span>
              <!---->
            </td>
            <td>
              <el-button type="text" @click="newTask(i.id)" :title="$t('index.details')">
                <!--//-->
                <i class="el-icon-info"></i>
              </el-button>
              <!---->
              <el-button type="text" @click="modelDelete(i.id)" style="color: red;" :title="$t('index.delete')">
                <!--//-->
                <i class="el-icon-delete"></i>
              </el-button>
              <!---->
            </td>
          </tr>
        </table>
      <div class="wei" v-show="kong">{{$t('resultManager.noDate')}}</div>
      </div>
      <!--<el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          @prev-click="handlePrevChange"
          @next-click="handleNextChange"
          :current-page="currentPage"
          :page-sizes="[10, 20, 30, 40]"
          :page-size="pagesize"
          layout="total, sizes, prev, pager, next, jumper"
          :total="totalCount">
        </el-pagination>-->
    </div>
  </div>
</template>

<script>
  import Breadcrumb from '../../components/Breadcrumb/Breadcrumb'
  import { addlanguage, languageModel, ModelDetail, modelUpdate, ModelDel, ModelTrain, speechdele, modelsearch, resetInit } from '@/api/newsex'
  export default {
    //  
    created() {
      this.LModel();
    },
    //  
    methods: {
      initialize() {
        resetInit().then((res) => {
          if(res.data.ret == 200) {
            this.LModel();
          }
        })
      },
      //    
      LModel() {
        languageModel().then((res) => {
          console.log(res);
          this.tableData = res.data.data;
        })
      },
      //    
      addlangu() {
        console.log(this.amendLanguagesList.gender, this.amendLanguagesList.desc, this.amendLanguagesList.mid);
        modelUpdate(this.amendLanguagesList.gender, this.amendLanguagesList.desc, this.amendLanguagesList.mid).then((res) => {
          console.log(res);
          if(res.data.ret == 200) {
            this.$message.success(this.$t('script.AmendSuccess')); //
            this.LModel();
          }
        })
      },
      //    
      onEsc() {
        this.nlguage = false;
        this.target = false;
        this.languagesList.language = '';
        this.languagesList.desc = '';
      },
      //    
      newlan() {
        this.target = true;
      },
      //    
      //    newla(){
      //      if(this.languagesList.language != ""){
      //        addlanguage(this.languagesList.language,this.languagesList.desc).then((res)=>{
      //          console.log(res);
      //          if(res.data.ret == 200){
      //            this.$message.success(this.$t('script.FoundSuccess'))//
      //            this.target = false;
      //            this.LModel();
      //          }if(res.data.ret == 400){
      //            this.$message.error(this.$t('script.LangRepetition'))//
      //          }
      //        })
      //      }else{
      //        this.$message.error(this.$t('script.SelectSex'));//
      //      }
      //    },
      //    
      beforeAvatarUpload(file) {
        const iswav = file.type === 'audio/wav';
        const ismp3 = file.type === 'audio/mp3';
        const isflac = file.type === 'audio/flac';
        const isLt10M = file.size / 1024 / 1024 < 50;
        if(!iswav && !ismp3 && !isflac) {
          this.$message.error(this.$t('script.FileRestrictions1')); //'.WAV,.MP3,.FLAC!'
        }
        if(!isLt10M) {
          this.$message.error(this.$t('script.FileRestrictions2')); //' 20MB!'
        }
        return (iswav || ismp3 || isflac) && isLt10M;
      },
      //    
      handleS(response, file, fileList) {
        this.i += 1;
        if(fileList.length == this.i) {
          this.i = 0;
          this.$message.success(this.$t('script.UploadSuccess')); //
          this.xli = false;
          fileList.splice(0, fileList.length);
          this.xq();
        }
      },
      //    
      plays(scope) {
        console.log(scope);
      },
      //    
      speechDele(id) {
        console.log(id);
        speechdele(id).then((res) => {
          console.log(res);
          if(res.data.ret == 200) {
            this.$message.success(this.$t('script.DelSuccess')); //
            this.xq();
          }
        })
      },
      //    
      modelS() {
        // console.log(this.form.mlanguage, this.form.status);
        modelsearch(this.form.mlanguage, this.form.status).then((res) => {
          // console.log(res);
          if(res.data.ret == 200){
            if(res.data.data.length != 0){
              this.kong = false;
              console.log(this.kong);
              console.log(res.data.data);
              this.tableData = res.data.data;
            }else{
              this.kong = true;
              console.log(this.kong + "123");
              console.log(res.data.data);
              this.tableData = res.data.data;
            }
          }
        })
      },
      //    
      Train() {
        console.log(this.speechi);
        if(this.speechi.length != 0) {
          this.xunlian = true;
          this.$alert(this.$t('script.Popout'), { //
            confirmButtonText: this.$t('script.Confirm') //
          });
          ModelTrain(this.Mid).then((res) => {
            console.log(res);
            if(res.data.ret == 200) {
              this.$message.success(this.$t('script.DrillSuccess')); //
              this.xunlian = false;
            }
          })
        } else {
          this.$message.error(this.$t('script.NoFile')); //
        }
      },
      //    
      xq() {
        ModelDetail(this.Mid).then((res) => {
          console.log(res);
          this.amendLanguagesList = res.data.data;
          this.speechi = res.data.data.speech;
          console.log(this.speechi);
          if(this.speechi.length == 0) {
            this.xli = true;
          }
        })
      },
      //    
      newTask(id) {
        console.log(id);
        this.Mid = id;
        this.amendLanguagesList.mid = id;
        this.nlguage = true;
        this.xq();
      },
      //    
      modelDelete(id) {
        this.$confirm(this.$t('script.DelFile'), this.$t('script.title'), {
          confirmButtonText: this.$t('script.Confirm'), //
          cancelButtonText: this.$t('script.Cancel'), //
          type: 'warning'
        }).then(() => {
          ModelDel(id).then((res) => {
            console.log(res);
            if(res.data.ret == 200) {
              this.$message.success(this.$t('script.DelSuccess')); //!
              this.LModel();
            }
          })
        }).catch(() => {
          this.$message.info(this.$t('script.CancelDel')); //
        });

      },
      //    
      submitUpload() {
        this.$refs.upload.submit();
      }
    },
    data() {
      return {
        kong:false,
        https: process.env.BASE_API + "/gre/model/speech/add",
        xli: true,
        Mid: '',
        i: 0,
        fileList: [],
        target: false,
        nlguage: false,
        xiaoshi: false,
        show_newg_lan: false,
        xiaoshi1: true,
        dianji1: true,
        xunlian: false,
        //      
        amendLanguagesList: {
          language: '',
          desc: '',
          mid: ''
        },
        //      
        languagesList: {
          language: '',
          desc: ''
        },
        form: {
          mlanguage: '',
          status: '',
          operation: ''
        },
        tableData: [],
        fileList: [],
        speechi: []
      }
    },
    computed: {
      //    
      UpData() {
        return {
          mid: this.amendLanguagesList.mid
        }
      }
    },
    components: {
      Breadcrumb
    }
  }
</script>


