//request
import request from '@/utils/request'
//
export function language(num) {
    return request.get('/user/set/language',{
      params:{
        num:num
      }
    })
}
export function getlanguage() {
    return request.get('/user/get/language',{
//    params:{
//      num:num
//    }
    })
}


