<style scoped>
    h1{
        background-color: red;
        color: white;
    }
    .taskmid>table>tr>td{
        padding: 10px 10px;
    }
    
    .taskmid>table>tr>th:first-child{
        width: 15px;
    }
    
    .taskmid>table>tr>td:first-child{
        width: 15px;
        text-align: center;
    }
    
</style>

<template>
  <div class="top">
    <Breadcrumb>
      <span slot="one"></span>
      <!---->
      <span slot="two"></span>
      <!---->
    </Breadcrumb>
    <div class="taskmid">
        <table>
            <tr>
                <th></th>
                <th></th>
                <th></th>
                <th></th>
                <th></th>
                <th></th>
                <th></th>
                <th></th>
                <th></th>
                <th></th>
            </tr>
            <tr v-for="(i,key) in table" :key="key">
                <td>{{key+1}}</td>
                <td>{{i.one}}</td>
                <td>{{i.two}}</td>
                <td>{{i.three}}</td>
                <td>{{i.four}}</td>
                <td>{{i.five}}</td>
                <td>{{i.six}}</td>
                <td>{{i.seven}}</td>
                <td>{{i.eight}}</td>
                <td>{{i.nine}}</td>
            </tr>
        </table>
    </div>
  </div>
</template>

<script>//
import Breadcrumb from '../../../components/Breadcrumb/Breadcrumb'
  export default {
    // 
    created() {

    },
    //  
     methods: {
       
    },
    data() {
      return {
        table:[
          {one:""},
          {two:""},
          {three:""},
          {four:""},
          {five:""},
          {six:""},
          {seven:""},
          {eight:""},
          {nine:""}
         ]
      }
    },
    components: {
      Breadcrumb
    }
  }
</script>


