import Vue from 'vue'
import Router from 'vue-router'
import index from '@/components/index'
import home from '@/components/home/home'
import homeTwo from '@/components/home/homeTwo'
import DefaultHome from '@/components/home/DefaultHome'
import userdata from '@/components/login/userdata'
import changepass from '@/components/login/ChangePass'
import drill from '@/components/language/drill'
import login from '@/components/login/UserLogin'
import disindex from '@/components/discern/index'
//import router from './router'result
import taskM from '@/components/task/taskM'
import result from '@/components/result/result'
import newfile from '@/components/aaa/new_file'
import voice from '@/components/VoiceIndexing/voice'
import nodeConfig from '@/components/configuration/nodeConfig'
import parameterConfig from '@/components/configuration/parameterConfig'
import AuthorityManagement from '@/components/AuthorityManagement/AuthorityManagement'
import organization from '@/components/AuthorityManagement/children/organization'
import role from '@/components/AuthorityManagement/children/role'
// import useradmin from '@/components/AuthorityManagement/children/useradmin'

Vue.use(Router)

const router = new Router({
  router,
//mode: 'hash',
  base: '', //
  routes: [{
      path: '/',
      name: 'login',
      component: login
    },{
      path: '/login',
      name: 'login',
      component: login
    },
//  {
//    path: '/login',
//    name: '',
//    component: login, //_import('Login/login')
//    hidden: true
//  },
    {
      path: '/index',
      name: 'index',
      component: index,
//    meta: {
//      requireAuth: true,  // 
//    },
      children: [
				{
          path: '/',
          name: 'DefaultHome',
          component: DefaultHome
        },
        {
          path: '/drill',
          name: 'drill',
          component: drill
        },
        {
          path: '/home',
          name: 'home',
          component: home
        },
        {
          path: '/homeTwo',
          name: 'homeTwo',
          component: homeTwo
        },
        {
          path: '/DefaultHome',
          name: 'DefaultHome',
          component: DefaultHome
        },
        {
          path: '/changepass',
          name: 'changepass',
          component: changepass
        },
        {
          path: '/userdata',
          name: 'userdata',
          component: userdata
        },
        {
          path: '/disindex',
          name: 'disindex',
          component: disindex
        },
        {
          path: '/taskM',
          name: 'taskM',
          component: taskM
        },
        {
          path: '/result/:id',
          name: 'result',
          component: result
        },
        {
          path: '/result',
          name: 'result',
          component: result
        },
        {
          path: '/voice',
          name: 'voice',
          component: voice
        },
        {
          path: '/newfile',
          name: 'newfile',
          component: newfile
        },
        {
          path: '/nodeConfig',
          name: 'nodeConfig',
          component: nodeConfig
        },
        {
          path: '/parameterConfig',
          name: 'parameterConfig',
          component: parameterConfig
        },
        // {
        //   path: '/AuthorityManagement',
        //   name: 'AuthorityManagement',
        //   component: AuthorityManagement,
        //   children:[
        {
          path: '/organization',
          name: 'organization',
          component: organization
        },{
          path: '/role',
          name: 'role',
          component: role
        }
        // ,
        // {
        //   path: '/useradmin',
        //   name: 'useradmin',
        //   component: useradmin
        // }
        //   ]
        // }
      ]
    }
  ]
})
