</template>

<script>
  import Breadcrumb from '../../components/Breadcrumb/Breadcrumb'
  import { newtaskadd, tasklist, taskStart, result, files, ftot, search, fileselect } from '@/api/newtask'
  import { list } from '@/api/antistop'
  export default {
    components: {
      Breadcrumb
    },
    //  
    computed: {
      UpData() {
        return {
          taskid: this.taskid
        }
      }
    },
    created() {
      //    
      this.loading(this.currentPage, this.pagesize);
      console.log("          ~~~~\n                \n               \n            \n    _..,------------,.._\n   ;'-.,------------------,.-';\n   |                          |\n                            ;\n    |\                        /\n  .-' `,._________________.,' '-.\n(       '----------------'       )\n `-=..________________________..-\n\n m17600534443@163.com");
    },
    methods: {
      //
      handleSizeChange: function(val) {
        this.pagesize = val;
        if(this.indx == 0) {
          this.loading(this.currentPage, this.pagesize);
        } else {
          this.seeker(this.stt, this.edt, this.currentPage, this.pagesize);
        }
      },
      //
      handleCurrentChange: function(val) {
        this.currentPage = val;
        if(this.indx == 0) {
          this.loading(this.currentPage, this.pagesize);
        } else {
          this.seeker(this.stt, this.edt, this.currentPage, this.pagesize);
        }
      },
      //
      handlePrevChange: function() {
        if(this.indx == 0) {
          this.loading(this.currentPage, this.pagesize);
        } else {
          this.seeker(this.stt, this.edt, this.currentPage, this.pagesize);
        }
      },
      //
      handleNextChange: function() {
        if(this.indx == 0) {
          this.loading(this.currentPage, this.pagesize);
        } else {
          this.seeker(this.stt, this.edt, this.currentPage, this.pagesize);
        }
      },
      //    
      seek() {
        this.indx = 1;
        this.currentPage = 1;
        if(this.tasksearch.starttime) {
          this.stt = Math.round(this.tasksearch.starttime.getTime() / 1000); //toLocaleDateString() this.tasksearch.starttime.getTime()
          console.log(parseFloat(this.stt));
          console.log(this.stt);
        } else {
          this.stt = this.tasksearch.starttime = "";
        }
        if(this.tasksearch.endtime) {
          this.edt = Math.round(this.tasksearch.endtime.getTime() / 1000); //toLocaleDateString() this.tasksearch.endtime.getTime()
          console.log(parseFloat(this.edt));
          console.log(this.edt);
        } else {
          this.edt = this.tasksearch.endtime = "";
        }
        this.seeker(this.stt, this.edt, this.currentPage, this.pagesize);
      },
      seeker(stt, edt, page, pagesize) {
        search(this.tasksearch.taskname, this.tasksearch.tasknum, this.tasksearch.tasktype, this.tasksearch.taskstatus, this.tasksearch.handler, stt, edt, page, pagesize).then((res) => {
          if(res.data.ret == 200) {
            this.tableData = res.data.data;
            this.totalCount = res.data.totalcount;
            if(this.totalCount == 0) {
              this.kong = true;
              this.fenye = false;
            } else {
              this.kong = false;
              this.fenye = true;
            }
          }
        })
      },
      //    
      loading(page, pagesize) {
        tasklist(page, pagesize).then((res) => {
          console.log(res.data);
          this.tableData = res.data.data;
          if(res.data.data == '') {
            this.kong = true;
            this.fenye = false;
            this.totalCount = res.data.totalcount;
          } else {
            this.kong = false;
            this.fenye = true;
            this.totalCount = res.data.totalcount;
          }
        })
        list().then((res) => {
          this.targetname = res.data.data;
          console.log(this.targetname);
        })
      },
      //    
      Remote() {
        files().then((res) => {
          console.log(res);
          if(res.data.ret == 200) {
            this.addfiles = res.data.data;
          }
          if(res.data.ret == 404) {
            this.$message.error(this.$t('script.NoFiles')); //
          } else {
            this.xiaoshi1 = false;
            this.xiaoshi = true;
            this.initall();
          }
        })

      },
      //    
      ftotfiles() {

        if(this.inde == "") {
          this.$message.error(this.$t('script.SelectTheFile')); //
        } else {
          if(this.displayfile.length == 0){
            this.$message.error("");//
          }else{
            this.show_task_add = false;
            this.xiaoshi1 = true;
            // this.dianji1 = true;
            this.xiaoshi = false;
            this.show_task = false;
            this.$message.success(this.$t('script.BeUploading')); //
            ftot(this.inde, this.taskid).then((res) => {
              console.log(res);
              if(res.data.ret == 200) {
                taskStart(this.taskid).then((res) => {
                  this.displayfile = [];
                  // this.selectItem=-2;
                  this.$message.success(this.$t('script.UploadComplete1')); //","
                  this.loading(this.currentPage, this.pagesize);
                })
              }
            })
          }
        }

      },
      //    
      onSubmit() {
        function changedouhao(str) {
          str = str.replace(//ig, ',');
          return str;
        }
        function unique(arr) {
          var res = [];
          for(var i = 0; i < arr.length; i++) {
            if(res.indexOf(arr[i]) == -1) {
              res.push(arr[i]);
            }
          }
          return res;
        }
        let arr = changedouhao(this.form.antistop).split(',');
        let ArrAy = unique(arr).join(',');
        console.log(ArrAy);
        if(this.form.enhance == 1 || this.form.cleaning == 1 || this.form.sr == 1 || this.form.ks == 1 || this.form.li == 1 || this.form.gi == 1 || this.form.idx == 1) {
          if(this.form.ks == 1) {
            if(this.form.typeId != '' || ArrAy != '') {
              if(this.form.idx == 1 && this.form.pnum == '') {
                this.$message.error(this.$t('script.NumberOfPeople')); //""
              } else {
                this.feng(ArrAy);
              }
            } else {
              this.$message.error(this.$t('script.Complete'))
            } //''
          } else {
            this.form.typeId = 0;
            if(this.form.idx == 1 && this.form.pnum == '') {
              this.$message.error(this.$t('script.NumberOfPeople')); //$t('logo.title')//""
            } else {
              this.feng(ArrAy);
            }
          }
        } else {
          this.$message.error(this.$t('script.SelectCategory')); //""
        }
      },
      keyup(val) {
        function changedouhao(str) {
          str = str.replace(//ig, ',');
          return str;
        }
        function unique(arr) {
          var res = [];
          for(var i = 0; i < arr.length; i++) {
            if(res.indexOf(arr[i]) == -1) {
              res.push(arr[i]);
            }
          }
          return res;
        }
        let arr = changedouhao(this.form.antistop).split(',');
        let ArrAy = unique(arr).join(',');
        console.log(ArrAy);

        console.log(ArrAy);
        var str = ArrAy;
        if(str.length > 0) {
          for(var i = str.length - 1; i >= 0; i--) {
            let unicode = str.charCodeAt(i);
            if(unicode > 65280 && unicode < 65375) {
              this.$message.error(this.$t('script.Import')); //""
              this.form.antistop = '';
              this.formaaa.pla = this.$t('script.Import') //""
            }
          }
        }
      },
      // watch:{
      //   filelist:function(val,old){
      //     console.log(val,old);
      //     if(val){
      //       this.$message.error("?")
      //     }else{
      //       this.$message.error("?")
      //     }
      //   }
      // },
      //    
      feng(ArrAy) {
        newtaskadd(this.form.name, this.form.tasknum, this.form.enhance, this.form.cleaning, this.form.sr, this.form.ks, this.form.li, this.form.gi, this.form.typeId, ArrAy, this.form.pnum, this.form.idx).then((res) => {
          if(res.data.ret == 200) {
            this.taskid = res.data.data.taskid;
            console.log(this.taskid);
            this.show_task_childen = false;
            this.show_task_add = true;
            this.xiaoshi = false;
            this.xiaoshi1 = true;
            this.dinaji1 = true;
          }
          if(res.data.ret == 401) {
            this.$message.error(this.$t('script.CreateModels1')); //''
          }
          if(res.data.ret == 402) {
            this.$message.error(this.$t('script.CreateModels2')); //''
          }
          if(res.data.ret == 403) {
            this.$message.error(this.$t('script.CreateModels3')); //''
          }
          if(res.data.ret == 405) {
            this.$message.error(this.$t('script.CreateModels4')); //''
          }
        }).catch((error) => {

          console.log(error);
        });
      },
      dianjia() {
        // this.dianji1 = false;
      },
      //    
      newTask() {
        this.show_task = true;
        this.show_task_childen = true;
        var myDate = new Date();
        var y = myDate.getFullYear().toString(), //
          m = ("0" + (myDate.getMonth() + 1)).slice(-2).toString(), //
          d = myDate.getDate().toString(), //
          h = myDate.getHours().toString(), //
          fen = myDate.getMinutes().toString(), //
          s = myDate.getMilliseconds().toString(); //
        var mydate = y + m + d + h + fen + s;
        var mydates = y + "" + m + "" + d + "" + h + "" + fen + "";
        this.form.tasknum = mydate;
        this.form.name = mydate;
      },
      //    
      guan() {
        this.form.antistop = '';
        this.form.typeId = "0";
        this.targetantistop = !this.targetantistop;
      },
      //    
      idex() {
        this.form.pnum = '';
        this.voice = !this.voice;
      },
      //    
      show() {
        this.targetantistop = false;
        this.voice = false;
        this.form.ks = 0;
        this.form.idx = 0;
      },
      //    
      conceal() {
        this.show_task = false;
        this.initall();
      },
      //    
      submitUpload() {
        this.$refs.upload.submit();
      },
      //    
      beforeAvatarUpload(file) {
        //      const iswav = file.type === 'audio/wav';
        //      const ismp3 = file.type === 'audio/mp3';
        //      const isflac = file.type === 'audio/flac';
        const isLt10M = file.size / 1024 / 1024 < 50;
        //      if (!iswav && !ismp3 && !isflac) {
        //        this.$message.error(this.$t('script.FileRestrictions1'));//'.WAV,.MP3,.FLAC!'
        //      }
        if(!isLt10M) {
          this.$message.error(this.$t('script.FileRestrictions2')); //' 20MB!'
        }
        //      iswav || ismp3 || isflac && 
        return isLt10M;
      },
      //    
      handleSuccess(response, file, fileList) {
        this.i += 1;
        this.taskid = response.data.taskid;
        this.taskfile_response = response.ret;
        if(fileList.length == this.i) {
          //        
          taskStart(this.taskid).then((res) => {
            this.$message.success(this.$t('script.UploadComplete1')); //""
            this.show_task = false;
            this.show_task_add = false;
            // this.dianji1 = true;
            this.i = 0;
            fileList.splice(0, fileList.length);
          })
          tasklist(this.currentPage, this.pagesize).then((res) => {
            this.tableData = res.data.data;
            if(res.data.data == '') {
              this.kong = true;
            } else {
              this.kong = false;
            }
          })
          this.initall();
        }

      },
      //    
      initall() {
        this.targetantistop = false;
        this.voice = false;
        this.form.enhance = 0;
        this.form.cleaning = 0;
        this.form.idx = 0;
        this.form.sr = 0;
        this.form.ks = 0;
        this.form.li = 0;
        this.form.typeId = "0";
        this.form.name = '';
        this.form.tasknum = '';
        this.form.gi = 0;
        this.form.antistop = ''
      },
      //    
      handleError(err, file, fileList) {
        //      console.log(this.type);
        this.i = 0;
      },
      change() {
        console.log(this.form.typeId);
        for(var i = 0; i < this.targetname.length; i++) {
          if(this.targetname[i].id == this.form.typeId) {
            this.form.typeName = this.targetname[i].name;
          }
        }
        console.log(this.form.typeName);
      },
      // 
      shut(){
        this.show_task = false;
        this.initall();
        this.show_task_add = false;
        this.fileList = [];
        // this.dianji1 = true;
      },
      shutTwo(){
        this.show_task = false;
        this.initall();
        this.displayfile = [];
        this.inde = '';
        this.show_task_add = false;
        // this.dianji1 = true;
      },
      // 
      retreat(){
        this.show_task_add = false;
        this.show_task_childen = true;
        this.fileList = [];
        // this.dianji1 = true;
      },
      retreatTwo(){
        this.displayfile = [];
        this.xiaoshi1 = true;
        this.xiaoshi = false;
      },
      // 
      // selected(i,key){
      //   this.selectItem = key;
      //   this.inde = key+1;
      //   console.log(i);
      //   fileselect(i.path).then((res)=>{
      //     if(res.data.ret == 200){
      //       console.log(res.data.data);
      //       function children(path, arr){

      //       }
      //       this.displayfile = res.data.data;
      //     }else if(res.data.ret == 404){
      //       this.displayfile = [];
      //       this.$message.error("!");
      //     }
      //   })
      // },
      // into(i,key){
      //   this.selectItem = key;
      //   this.inde = key+1;
      //   console.log(i.path);
      //   // console.log(this.addfiles);
      //   if(i.type == "dir"){
      //     this.addfiles = this.displayfile;//parentname
      //     fileselect(i.path).then((res)=>{
      //       if(res.data.ret == 200){
      //         console.log(res.data.data);
      //         this.displayfile = res.data.data;
      //       }else if(res.data.ret == 404){
      //          this.$message.error("!"); 
      //          this.displayfile = [];
      //       }
      //     })
      //   }else{
      //     this.$message.error("");
      //   }
      // },
      handleNodeClick(i) {
        console.log(i);
        // this.selectItem = key;
        this.inde = i.label;
        console.log(this.inde);
        console.log(i);
        fileselect(i.path).then((res)=>{
          if(res.data.ret == 200){
            console.log(res.data.data);
            this.displayfile = res.data.data;
          }else if(res.data.ret == 404){
            this.displayfile = [];
          }
        })
      }
    },
    data() {
      return {
        // selectItem:-1,
        stt: '',
        edt: '',
        indx: 0,
        currentPage: 1,
        totalCount: 0,
        pagesize: 10,
        fenye: false,
        formaaa: {
          typeId: '',
          pla: this.$t('script.hint') //'","'
        },
        targetantistop: false,
        voice: false,
        addfiles: [],
        displayfile:[],
        inde: "",
        i: 0,
        // dianji1: true,
        xiaoshi1: true,
        xiaoshi: false,
        kong: false,
        taskfile_response: "",
        tasklists: [],
        show_task: false,
        show_task_childen: false,
        show_task_add: false,
        suc: false,
        taskid: "",
        tasksearch: {
          taskname: '',
          tasknum: '',
          tasktype: '',
          taskstatus: '',
          handler: '',
          starttime: '',
          endtime: ''
        },
        fileList: [],
        tableData: [],
        targetname: [],
        form: {
          typeId: '',
          typeName: '',
          name: '',
          tasknum: '',
          antistop: '',
          pnum: '',
          enhance: 0,
          cleaning: 0,
          sr: 0,
          ks: 0,
          li: 0,
          gi: 0,
          idx: 0
        },
        http: process.env.BASE_API + "/task/upload",
        defaultProps: {
          children: 'children',
          label: 'label'
        }
      }
    }

  }
</script>


